<template>
  <transition name="move">
    <div class="detailWrapper" ref="detaiWrapper" v-show="showD">
      <div class="foodDetail">
        <div class="back" @click="showD=!showD">
          <i class="icon-arrow_lift"></i>
        </div>
        <img :src="fd.image">
        <div class="info">
          <div class="title"></div>
          <div class="desc">
            <span>月售{{fd.sellCount}}份</span>
            <span>好评率{{fd.rating}}%</span>
          </div>
          <div class="price">${{fd.price}}</div>
          <transition name="fade">
            <div v-show="typeof fd.count==='undefined'|| fd.count===0" class="shopCart" @click="add($event)"><div class="text">加入购物车</div></div>
          </transition>
          <cartcontrol ref="cartcontrol" :food="fd"></cartcontrol>
        </div>
        <div class="divider"></div>
        <div class="desc">
          <div class="title">商品介绍</div>
          <div class="content"></div>
        </div>
        <div class="divider"></div>
        <div class="evaluation">
          <div class="title">商品评价</div>
          <div class="classify">
            <!--多个值的添加-->
            <span v-for="(item, index) in classify" class="item" :class="{'active': item.active, 'bad': index === 2, 'badActive': index===2&&item.active}" @click="classClick(item) ">{{item.name}}({{item.count}})</span>
          </div>
          <div class="switch" @click="flag = !flag">
            <span class="icon-check_circle" :class="{'on': flag}"></span>
            <span class="text">只看有内容的评价</span>
          </div>
          <div class="evel-list">
            <ul>
              <li class="evel" v-for="item in commontss">
                <div class="userInfo">
                  <div class="time">{{item.rateTime}}</div>
                  <div class="user">
                    <span>{{item.rateTime}}</span>
                    <img :src="item.avatar" width="12" height="12">
                  </div>
                </div>
                <div class="content">
                  <span class="icon" :class="item.rateType?'icon-thumb_down':'icon-thumb_up'"></span>
                  <span class="text">{{item.text}}</span>
                </div>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </div>
  </transition>
</template>
<script>
import Scroll from 'better-scroll'
import cartcontrol from './cartcontrol.vue'
export default {
  props: ['fd'],
  data () {
    return {
      showD: false,
      classify: [
        {name: '全部', count: 0, commont: [], active: true},
        {name: '推荐', count: 0, commont: [], active: false},
        {name: '吐槽', count: 0, commont: [], active: false}
      ],
      commonts: [],
      flag: true
    }
  },
  methods: {
    foodShow () {
      this.showD = true
      this.$nextTick(() => {
        this.commonts = this.fd.ratings
        this.fd.ratings.forEach((val) => {
          this.classify[0].count++
          this.classify[0].commont.push(val)
          if (val.rateType) {
            this.classify[2].count++
            this.classify[2].commont.push(val)
          } else {
            this.classify[1].count++
            this.classify[1].commont.push(val)
          }
        })
      })
      this.$nextTick(() => {
        if (!this.sc) {
          this.sc = new Scroll(this.$refs['detaiWrapper'], {
            click: true
          })
        } else {
          // 重新计算高度
          this.sc.refresh()
        }
      })
    },
    add (event) {
      this.$refs['cartcontrol'].addCart(event)
    },
    classClick (item) {
      this.commonts = item.commont
      this.classify.forEach((ac) => {
        ac.active = false
      })
      item.active = true
    },
    updatesc () {
      this.$nextTick(() => {
        // 用滚动插件所要用到的重新计算
        this.sc.refresh()
      })
    }
  },
  mounted () {
  },
  computed: {
    commontss () {
      // 先注册一个$nextTick  2.更新 3.检测到dom更新完，然后再执行$nextTick 回调
      this.updatesc();
      let arr = []
      // 只看有内容的评价
      if (this.flag) {
        this.commonts.forEach(val => {
          if (val.text.length > 0) {
            arr.push(val)
          }
        })
        return arr
      } else {
        // 看全部评价
        return this.commonts
      }
    }
  },
  components: {
    cartcontrol
  }
}
</script>



