<template>
  <div class="goods">
    <div class="menu-wrapper" ref="menuWrapper">
      <ul>
        <li class="menu-item" v-for="item in datas.goods">
          <span class="text"><span class="iconMap" v-show="item.type>0" :class="icon[item.type]"></span>{{item.name}}</span>
        </li>
      </ul>
    </div>
    <div class="foods-wrapper" ref="foodsWrapper">
      <ul>
        <li v-for="item in datas.goods" class="food-list food-list-hook">
          <h1 class="title">{{item.name}}</h1>
          <ul>
            <li v-for="food in item.foods" class="food-item" @click="xiangqing(food)">
              <div class="icon">
                <img :src="food.icon" width="57" height="57">
              </div>
              <div class="content">
                 <h2 class="name">{{food.name}}</h2>
                <p class="description">{{food.description}}</p>
                <div class="sell-info">
                  <span class="sellCount">月售{{food.sellCount}}份</span>
                  <span class="rating">好评率{{food.rating}}</span>
                  <div class="price">
                    <span class="newPrice">${{food.price}}</span>
                  </div>
                  <div class="cartcontrol-wrapper">
                    <cartcontrol :food="food"></cartcontrol>
                  </div>
                </div>
              </div>
            </li>
          </ul>
        </li>
      </ul>
    </div>
    <shopcart :food="food" :deliveryPrice="datas.seller.deliveryPrice" :minPrice="datas.seller.minPrice"></shopcart>
    <!--<div class="shopCart"></div>-->
    <!--<div class="detailWrapper"></div>-->
    <!--当我们用ref绑定组件的时候,就可以用组件里面的东西-->
    <foodDetail :fd="fd" ref="foodDetail"></foodDetail>
  </div>
</template>
<script>
import icon from './mods/icon'
import Scroll from 'better-scroll'
import shopcart from './mods/shopCart.vue'
import foodDetail from './mods/foodDetail.vue'
import cartcontrol from './mods/cartcontrol.vue'
export default {
  props: ['datas'],
  data () {
    return {
      icon,
      fd: {}
    }
  },
  mounted () {
    // dom 更新完之后 就 === window.onload
    // dom 更新完之后 => new Scroll() => ajax获取到数据 => dom 更新完
  },
  computed: {
    food () {
      let arr = []
      // 回调函数的参数   1.value (值)  2. index (索引)
      // this.datas.goods.forEach( goods => {  相当与  for(var goods in goods)
      this.datas.goods.forEach(goods => {
        goods.foods.forEach(foods => {
          if (foods.count > 0) {
            arr.push(foods)
          }
        })
      })
      return arr
    }
  },
  methods: {
    scroll () {
      /* eslint-disable no-new */
      new Scroll(this.$refs['foodsWrapper'], {
        click: true
      })
      new Scroll(this.$refs['menuWrapper'], {
        click: true
      })
    },
    xiangqing (food) {
      this.fd = food
      this.$refs['foodDetail'].foodShow()
    }
  },
  components: {
    shopcart,
    cartcontrol,
    foodDetail
  }
}
</script>



