<template>
  <div class="cartcontrol">
    <input type="hidden" v-model="vxfood">
    <transition name="fadeRotate">
      <div class="cart-decrease" v-show="food.count>0" @click.stop="decreaseCart($event)">
        <span class="icon-remove_circle_outline inner"></span>
      </div>
    </transition>
    <div class="cart-count" v-show="food.count>0">{{food.count}}</div>
    <!--@click.stop 这个是vue里的阻止冒泡-->
    <div class="cart-add" @click.stop="addCart($event)">
      <i class="icon-add_circle"></i>
    </div>
  </div>
</template>
<script>
import Vue from 'vue'
import {mapMutations, mapState} from 'vuex'
export default {
  props: ['food'],
  methods: {
    ...mapMutations([
      'vxaddCart',
      'vxdecreaseCart'
    ]),
    addCart (e) {
      if (!e._constructed) {
        // _constructed 是滚动事件里添加的一个值
        // 没有这这值的是pc端
        return false
      }
      if (typeof this.food.count === 'undefined') {
        //  this.$set(this.food.cound,'num',0)
        // 直接通过 Vue 添加 全局   就可以直接往数据里添加数据   如果是通过 this.$set(this.food.cound,'num',0) 局部方法的话  就要通过触发事件
        Vue.set(this.food, 'count', 0)
        // Vue.set(this.food, 'active', true) 当我点击的时候添加一个 active 项，值为true  识别是否点击过
        Vue.set(this.food, 'active', true)
      }
      this.food.count++
      if (this.food.active) {
        this.vxaddCart(this.food)
        this.food.active = false
      }
    },
    decreaseCart (e) {
      if (!e._constructed) {
        // _constructed 是滚动事件里添加的一个值
        // 没有这这值的是pc端
        return false
      }
      this.food.count--
      if (this.food.count === 0) {
        this.vxdecreaseCart(this.food)
      }
    }
  },
  computed: {
    ...mapState([
      'vxfood'
    ])
  }
}
</script>



