<template>
  <div id="app">
    <hd v-bind:seller="datas.seller"></hd>
    <div class="tab">
      <div class="tab-item">
        <router-link to="/goods">商品</router-link>
      </div>
      <div class="tab-item">
        <router-link to="/ratings">评论</router-link>
      </div>
      <div class="tab-item">
        <router-link to="/seller">商家</router-link>
      </div>

    </div>
    <!--keep-alive把加载过的组件缓存起来-->
    <keep-alive>
      <router-view :datas="datas" ref="abc"></router-view>
    </keep-alive>
  </div>
</template>

<script>
import head from './components/mods/head.vue'
import axios from 'axios'
export default {
  name: 'App',
  data () {
    return {
      datas: {
        // 用户信息
        seller: {},
        // 商品
        goods: [],
        // 评论
        ratings: []
      }
    }
  },
  methods: {

  },
  mounted () {
    axios.get('/static/data.json').then((res) => {
      console.log(res)
      this.datas.goods = res.data.goods
      this.datas.seller = res.data.seller
      this.datas.ratings = res.data.ratings
      // 用获取节点的方法 ref 来绑定 组件，获取的值就是 组件对象  那么这个插件就是 dom 更新完之后 => new Scroll() => ajax获取到数据 => dom 更新完  只有 dom 更新完  才能window.onload
      console.log(this.$refs['abc'])
      // dom 更新完之后 就 === window.onload
      // dom 更新完之后 => new Scroll() => ajax获取到数据 => dom 更新完
      //  $nextTick是等数据加载完之后才执行的函数
      this.$nextTick(() => {
        this.$refs.abc.scroll()
      })
    })
  },
  components: {
    hd: head
  }
}
</script>
<!--
scoped 写上的时候  就是当前这个模板 和 当前所有的子模板 应用这个样式
-->
<style scoped>
.router-link-exact-active{
  color:red!important;
}
</style>



