// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack安装步骤.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
// vuex的引入 是文件夹的时候  引入的是文件夹下的名字为index.js的文件
import store from './vuex'

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



