<template>
  <div class="ratingsWrapper" ref="ratingsWrapper">
    <div class="ratings-content">
      <div class="info">
        <div class="mark">
          <div class="num">{{datas.seller.score}}</div>
          <div class="text">综合评分</div>
          <div class="contrast">高于周边商家{{datas.seller.rankRate}}%</div>
        </div>
        <div class="stars">
          <div class="serviceScore">
            <span class="text">服务态度</span>
            <star :size="36" :score="datas.seller.serviceScore"></star>
            <div class="num">{{datas.seller.serviceScore}}</div>
          </div>
          <div class="foodScore">
            <span class="text">商品评分</span>
            <star :size="36" :score="datas.seller.serviceScore"></star>
            <div class="num">{{datas.seller.serviceScore}}</div>
          </div>
        </div>
      </div>
      <div class="divider"></div>
      <div class="evaluation">
        <div class="classify">
          <span class="item" v-for="(item, index) in classify"
                @click="evel(item)"
                :class="{'active': item.active, 'bad': index===2, 'badActive': index===2&&item.active}">
            {{item.name}}
            <span class="count">{{item.count}}</span>
          </span>
        </div>
        <div class="switch" @click="show=!show">
          <span class="icon-check_circle" :class="{'on': show}"></span>
          <span class="text">只看内容评价</span>
        </div>
        <div class="evel-list">
          <ul>
            <li class="evel" v-for="item in commontss">
              <div class="avatar">
                <img :src="item.avatar" width="28" height="28">
              </div>
              <div class="content">
                <div class="user">
                  <span class="name">{{item.username}}</span>
                  <span class="rateTime">{{item.rateTime}}</span>
                </div>
                <div class="star-wrapper">
                  <star :size="24" :score="item.score"></star>
                  <span class="deliveryTime">{{item.deliveryTime}}分钟送达</span>
                </div>
                <div class="text">{{item.text}}</div>
                <div class="recommend">
                  <span class="icon" :class="item.rateType?'icon-thumb_down':'icon-thumb_up'"></span>
                  <span class="dish" v-for="i in item.recommend">{{i}}</span>
                </div>
              </div>
            </li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import axios from 'axios'
import Scroll from 'better-scroll'
import star from './mods/star.vue'
export default {
  props: ['datas'],
  data () {
    return {
      ratings: [],
      classify: [
        {name: '全部', active: true, count: 0, commont: []},
        {name: '推荐', active: false, count: 0, commont: []},
        {name: '吐槽', active: false, count: 0, commont: []}
      ],
      show: true,
      commonts: []
    }
  },
  // mounted 组件加载完成就触发  有 window.onload的功能
  mounted () {
    axios.get('/static/data.json').then(res => {
      this.ratings = res.data.ratings
      this.commonts = res.data.ratings
      this.ratings.forEach(val => {
        this.classify[0].count++
        this.classify[0].commont.push(val)
        if (val.rateType) {
          this.classify[2].count++
          this.classify[2].commont.push(val)
        } else {
          this.classify[1].count++
          this.classify[1].commont.push(val)
        }
      })
      this.$nextTick(() => {
        this.sc = new Scroll(this.$refs['ratingsWrapper'], {
          click: true
        })
      })
    })

  },
  methods: {
    evel (item) {
      this.classify.forEach(val => {
        val.active = false
      })
      this.commonts = item.commont
      item.active = true
    }
  },
  computed: {
    // 计算方法
    commontss () {
      if (this.show) {
        let arr = []
        this.commonts.forEach(val => {
          if (val.text.length != 0) {
            arr.push(val)
          }
        })
        return arr
      } else {
        return this.commonts
      }
    }
  },
  // activated 方法 就是不管有没有被缓存  都会被执行
  activated () {
  // 这个是组件缓存数据更新
  },
  components: {
    star
  }
}
</script>



