export default {
  // 默认第一个参数就是vuex 里state 的数据  第二个参数才是传过来的
  vxaddCart (state, val) {
    console.log(arguments)
    state.vxfood.push(val)
  },
  vxdecreaseCart (state, food) {
    state.vxfood.forEach((value, index) => {
      if (value.name === food.name) {
        state.vxfood.splice(index, 1)
      }
    })
  }
}



