<template>
  <div class="seller-wrapper" ref="sellerWrapper">
    <div class="seller-content">
      <div class="info">
        <div class="title">
          <div class="text">{{datas.seller.name}}</div>
          <div class="star-wrapper">
            <star :size="36" :score="datas.seller.score"></star>
            <div class="rate-count">({{datas.seller.ratingCount}})</div>
            <div class="sell-count">月售{{datas.seller.sellCount}}单</div>
          </div>
          <div class="collect" @click="show=!show">
            <span class="icon-favorite" :class="{'active': show}"></span>
            <span class="text">{{show ? '以收藏' : '收藏'}}</span>
          </div>
          <div class="remark">
            <div class="block">
              <h2>起送价</h2>
              <div class="content">
                <span class="num">{{datas.seller.minPrice}}</span>元
              </div>
            </div>
            <div class="block">
              <h2>商家配送</h2>
              <div class="content">
                <span class="num">{{datas.seller.deliveryPrice}}</span>元
              </div>
            </div>
            <div class="block">
              <h2>平均配送时间</h2>
              <div class="content">
                <span class="num">{{datas.seller.deliveryTime}}</span>元
              </div>
            </div>
          </div>
          <div class="activities">
            <div class="bulletin">
              <h1>公告与活动</h1>
              <div class="content">{{datas.seller.bulletin}}</div>
            </div>
          </div>
          <div class="supports">
            <ul>
              <li class="item" v-for="item in datas.seller.supports">
                <span class="iconMap" :class="icon[item.type]"></span>
                <span class="text">{{item.description}}</span>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import icon from './mods/icon'
import Scroll from 'better-scroll'
import star from './mods/star.vue'
export default {
  props: ['datas'],
  data () {
    return {
      show: false,
      icon
    }
  },
  created () {
    // 生命周期  在这里面写滚动插件 就要添加this.$nextTick()方法了  this.$nextTick()是dom更新完执行的方法

  },
  mounted () {
    /* eslint-disable no-new */
    // 这里为什么可以直接new 滚动插件  是因为 数据早就下载好了 再一个在这个组件里没有方法或计算属性去改本页的数据，所以不需要添加this.$nextTice()方法
    new Scroll(this.$refs['sellerWrapper'], {
      click: true
    })
  },
  components: {
    star
  }
}
</script>



