<template>
  <div class="shopCart">
    <!--{{food}}-->
    <div @click="showF" class="content">
      <div class="content-left">
        <div class="logo-wrapper">
          <div class="logo active">
            <i class="icon-shopping_cart"></i>
          </div>
          <div class="badge" v-show="totalCount>0">{{totalCount}}</div>
        </div>
        <!--:class="{'active': totalPrice}" 这个意思就是 当我们的totalPrice有值的时候  也就大于0的时候 就为true 就添加class类名 active-->
        <div class="price" :class="{'active': totalPrice}">${{totalPrice}}</div>
        <div class="desc" v-show="totalPrice < 99">需要配送费￥{{deliveryPrice}}元</div>
      </div>
      <div class="content-right" :class="{'enough': totalPrice>minPrice}">
        <!--<div class="pay">还差{{minPrice}}元</div>-->
        <div class="pay">{{payDesc}}</div>
      </div>
    </div>
    <transition name="transHeight">
      <div class="shopcart-list" v-show="totalPrice>0 && show">
        <div class="list-header">
          <h1 class="title">购物车</h1>
          <span @click="empty" class="empty">清空</span>
        </div>
        <div ref="listcontent" class="list-content">
          <ul>
            <li v-for="item in food" class="food">
              <span class="name">{{item.name}}</span>
              <div class="price">
                <span>{{item.price}}</span>
              </div>
              <div class="cartcontrol-wrapper">
                <cartcontrol :food="item"></cartcontrol>
              </div>
            </li>
          </ul>
        </div>
      </div>
    </transition>

  </div>
</template>
<script>
import cartcontrol from './cartcontrol.vue'
import Scroll from 'better-scroll'
export default {
  // 配送费  起步价 数量大于零的商品
  props: ['deliveryPrice', 'minPrice', 'food'],
  data () {
    return {
      show: false
    }
  },
  computed: {
    totalPrice () {
      let total = 0
      this.food.forEach(food => {
        total += food.price * food.count
      })
      return total
    },
    totalCount () {
      let count = 0
      this.food.forEach(food => {
        count += food.count
      })
      return count
    },
    // 还差多少元起送
    payDesc () {
      if (this.totalPrice === 0) {
        return `$${this.minPrice}起送`
      } else if (this.totalPrice < this.minPrice) {
        return `还差${this.minPrice - this.totalPrice}起送`
      } else {
        return '去结算'
      }
    }
  },
  components: {
    cartcontrol
  },
  methods: {
    empty () {
      this.food.forEach(val => {
        val.count = 0
      })
      this.show = false
    },
    showF () {
      this.show = !this.show
      if (!this.sc) {
        // 等待dom更新完
        this.$nextTick(function(){
          // new 进行赋值之后就不会报错
          this.sc = new Scroll(this.$refs['listcontent'], {
            click: true
          })
        })
      } else {
        // 强制重新计算高度
        this.sc.refresh()
      }
    }
  }
}
</script>



